* {
  margin: 0;
  padding: 0;
}
body {
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  background: #e6e6e6;
}
.css-6hp17o-MuiList-root-MuiMenu-list {
  padding: 0 !important;
}
.w-100 {
  width: 100%;
}
.text-muted {
  color: #a5a5a5 !important;
}
.text-success {
  color: #2dce89 !important;
}
.text-warning {
  color: #fb6340 !important;
}
.mb3 {
  margin-bottom: 1rem !important;
}
.navbar {
  a {
    font-size: 0.875rem;
    padding: 6px 8px;
    color: white;
    text-decoration: none;
  }
}
.css-hip9hq-MuiPaper-root-MuiAppBar-root {
  background-color: #06a1a7 !important;
}
.css-11xur9t-MuiPaper-root-MuiTableContainer-root {
  box-shadow: none !important;
}
.table-bordered thead td,
.table-bordered thead th {
  border-bottom-width: 2px;
}
.table-bordered td,
.table-bordered th {
  border: 1px solid #dee2e6;
}
.table thead th {
  vertical-align: bottom;
  border-bottom: 2px solid #dee2e6;
}
.table-striped tbody tr:nth-of-type(odd) {
  background-color: #00000014;
}
.table-bordered td {
  padding: 8px 20px;
}
.customnav {
  display: block;
  padding: 5px 10px;
  text-decoration: none;
  color: #000;
  font-size: 15px;
}
.wrapperbox {
  padding: 2em 0;
  .img {
    img {
      width: 200px;
      display: block;
      margin: auto;
      margin-bottom: 1rem;
    }
  }
  .formbox {
    h1,
    h3 {
      font-family: "Poppins", sans-serif;
    }
    h1 {
      font-size: 23px;
      font-weight: 500;
    }
    h3 {
      font-size: 16px;
      font-weight: 400;
    }
  }

  .wallet {
    display: flex;
    align-items: center;
    font-size: 16px;
    justify-content: flex-start;
    font-weight: 500;
  }
  .tktbox {
    display: flex;
    align-items: center;
    .box {
      background-color: #ff4545;
      padding: 5px 20px;
      margin-right: 10px;
      border-radius: 4px;
      color: #fff;
      font-size: 12px;
      text-align: center;
      &:last-child {
        background-color: #47c44c;
      }
    }
  }
}
